h1 {
  color: #27aedb;
  text-align: center;
  }
  

button{
  color: #27aedb;
  text-align: center;
  justify-content: center;
  align-items: center;
}

.bg-pink{
background-color:pink;
}

#main {
  width: 70%;
  margin: auto;
}

.new {
  width: 100%;
  display: flex;
  align-items: center;
  margin: 10px;
}

textarea#task {
  width: 70%;
  height: 40px;
  margin: 10px;
  background: lightgray;
  margin-left: 0px;
}

button#btn {
  height: 20px;
  margin: 5px;
}

.list {
  display: flex;
  align-items: center;
  height: 40px;
  border: 2px solid black;
  margin: 8px;
  justify-content: space-between;
  background-color: lightsteelblue;
  margin: 10px;
}

.listitem {
  margin: 5px;
  width: 100%;
}

.editTask {
  width: 100%;
  height: 20px;
}

.listactions {
  width: 150px;
}

.listactions button {
  margin: 5px;
}

.login {
  display: flex;
  flex-direction: column;
  width: 200px;
  margin: auto;
  margin-top: 50px;
  height: 150px;
  justify-content: space-around;
}

.error {
  color: red;
  font-size: 10px;
  width: 100%;
  text-align: center;
}

.user {
  display: flex;
  margin: 10px;
  justify-content: space-between;
}